.panel {
    border: 3px solid black;
    padding: 10px;
    -webkit-box-shadow: 5px 5px 5px #2f4f4f;
            box-shadow: 5px 5px 5px #2f4f4f;
}
.savedContainer {
    border: 1px solid black;
    padding: 5px;
    margin: 5px;
    -webkit-box-shadow: 3px 3px 3px #2f4f4f;
            box-shadow: 3px 3px 3px #2f4f4f;
}
.viewButton,
.noteButton {
    display: inline-block;
    width: 49%;
    -webkit-box-shadow: 2px 2px 2px #2f4f4f;
            box-shadow: 2px 2px 2px #2f4f4f;
}
.deleteButton {
    width: 99%;
}


